@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
big,
blockquote,
body,
canvas,
caption,
center,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
embed,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
output,
p,
pre,
q,
ruby,
s,
samp,
section,
small,
span,
strike,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
tt,
u,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

#content-area {
  overflow: auto;
  width: 100%;
  min-height: 100%; }

#filter {
  background: #faf7f5;
  width: 30%;
  max-width: 300px;
  display: inline-block;
  float: left; }
  #filter .inside {
    padding: 20px;
    max-width: 300px; }
  #filter h4 {
    font-size: 0.9rem;
    margin-bottom: 25px;
    font-weight: 500px;
    text-transform: uppercase; }
  #filter select {
    background: transparent;
    padding: 10px;
    font-size: 14px;
    font-weight: 500;
    color: #1b1b1b;
    display: block;
    margin-bottom: 25px;
    width: 100%; }
  #filter .filters .title {
    font-size: 0.9rem;
    display: block;
    margin-bottom: 1rem;
    color: #828d9d; }
  #filter .filters input[type="text"] {
    border-radius: 5px;
    font-size: 1rem;
    padding: 0.4rem;
    width: 45%;
    display: inline-block;
    margin-right: 2.5%;
    margin-bottom: 1rem; }
  #filter .filters.extras label {
    display: block;
    width: 100%;
    padding: 10px 0; }
    #filter .filters.extras label input[type="checkbox"] {
      float: right; }

#listings {
  width: 70%;
  float: left;
  display: inline-block;
  padding: 15px;
  background: #fffeff; }
  #listings .search-area input[type="text"] {
    width: 70%;
    background: transparent;
    color: #828d9d;
    font-size: 2rem;
    border: none;
    border-bottom: 1px solid #828d9d;
    font-weight: 100;
    margin-bottom: 20px; }
  #listings .sortby-area {
    margin-bottom: 20px; }
    #listings .sortby-area .results {
      font-size: .8rem;
      color: #828d9d;
      display: inline-block; }
    #listings .sortby-area .sort-options {
      float: right; }
      #listings .sortby-area .sort-options select {
        font-size: .9rem;
        font-weight: 300;
        border: 1px solid #dae3ef;
        background: transparent;
        display: inline-block;
        margin-right: 15px;
        vertical-align: middle; }
      #listings .sortby-area .sort-options .view {
        display: inline-block; }
        #listings .sortby-area .sort-options .view .fa {
          margin-right: 15px;
          display: inline-block; }
  #listings .listings-results {
    margin-bottom: 60px; }
    #listings .listings-results .listing {
      background: #fff;
      border-radius: 5px;
      box-shadow: 0px 9px 54px -4px #e0e0e0; }
    #listings .listings-results .listing-image {
      color: white;
      padding: 15px;
      position: relative;
      background: url("https://ycdn.space/h/2018/12/003-modena-55-metricon-1050x733.webp") no-repeat center center;
      background-size: cover;
      min-height: 200px;
      -webkit-border-top-left-radius: 5px;
      -webkit-border-top-right-radius: 5px;
      -moz-border-radius-topleft: 5px;
      -moz-border-radius-topright: 5px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px; }
      #listings .listings-results .listing-image:hover .details {
        opacity: 1; }
      #listings .listings-results .listing-image:hover .address {
        opacity: 0; }
      #listings .listings-results .listing-image .address {
        position: absulute;
        font-size: 1.1rem;
        bottom: 20px;
        opacity: 1;
        transition: all .4s ease-in-out;
        text-shadow: 0px 1px 4px #5b5b5b; }
      #listings .listings-results .listing-image .details {
        transition: all .4s ease-in-out;
        opacity: 0;
        padding: 30px;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        -webkit-border-top-left-radius: 5px;
        -webkit-border-top-right-radius: 5px;
        -moz-border-radius-topleft: 5px;
        -moz-border-radius-topright: 5px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#3869ff+0,a149ff+100 */
        background: rgba(56, 105, 255, 0.8);
        /* Old browsers */
        /* FF3.6-15 */
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, rgba(56, 105, 255, 0.8) 0%, rgba(161, 73, 255, 0.8) 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3869ff', endColorstr='#a149ff',GradientType=1 );
        /* IE6-9 fallback on horizontal gradient */ }
        #listings .listings-results .listing-image .details .user-img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          border: 2px solid white;
          display: inline-block;
          background: url("https://ycdn.space/h/2018/12/003-modena-55-metricon-1050x733.webp") no-repeat center center;
          background-size: cover; }
        #listings .listings-results .listing-image .details .user-details {
          vertical-align: top;
          display: inline-block; }
          #listings .listings-results .listing-image .details .user-details .user-name {
            font-size: 0.9rem;
            font-weight: 300;
            display: block;
            color: white;
            margin-top: 4px;
            margin-bottom: 5px; }
          #listings .listings-results .listing-image .details .user-details .post-date {
            display: block;
            color: rgba(255, 255, 255, 0.7);
            font-size: 0.9rem; }
        #listings .listings-results .listing-image .details .listing-details {
          margin-top: 20px;
          margin-bottom: 10px; }
          #listings .listings-results .listing-image .details .listing-details .fa {
            display: inline-block;
            font-size: 20px;
            margin-right: 15px; }
          #listings .listings-results .listing-image .details .listing-details span {
            font-weight: 300;
            font-size: 0.8rem;
            display: inline-block;
            vertical-align: top;
            position: relative;
            top: 3px; }
          #listings .listings-results .listing-image .details .listing-details .floor-space {
            margin-bottom: 10px; }
    #listings .listings-results .bottom-info {
      padding: 20px 15px; }
      #listings .listings-results .bottom-info .price {
        font-size: 1rem;
        display: block;
        margin-bottom: 10px;
        color: #828d9d; }
      #listings .listings-results .bottom-info .location {
        font-size: 1.3rem;
        display: block;
        color: #dae3ef; }

.view-btn {
  background: black;
  padding: 8px;
  border-radius: 5px;
  text-align: center;
  font-size: 0.9rem;
  max-width: 150px;
  transition: background .2s ease-in-out; }
  .view-btn:hover {
    background: #ff718e;
    cursor: pointer; }

#pagination {
  text-align: center; }
  #pagination .pages {
    margin: 0 auto;
    background: white;
    display: inline-block;
    box-shadow: 0px 9px 54px -4px #e0e0e0; }
    #pagination .pages li {
      display: inline-block;
      padding: 20px 30px; }
      #pagination .pages li.active, #pagination .pages li:hover {
        color: white;
        font-weight: 700;
        background: #ff718e;
        cursor: pointer; }

.col-md-3 {
  width: 25%;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px; }

.col-md-9 {
  width: 75%;
  display: inline-block; }

.col-lg-6 {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px; }

*,
body,
html {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box; }

header {
  background: #0c002b;
  color: white;
  overflow: auto;
  padding: 20px 15px; }
  header .logo {
    float: left; }
  header nav {
    float: right; }
    header nav a {
      color: white;
      text-decoration: none;
      padding: 7px 20px;
      text-transform: capitalize;
      font-weight: 300;
      font-size: .9rem; }
      header nav a:hover {
        color: #ff718e; }
      header nav a.register-btn {
        background: #ff718e;
        padding: 7px 25px;
        text-align: center;
        color: white;
        border-radius: 5px; }

